.weather-detail {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -ms-flex-direction: column;
      flex-direction: column;
}

.weather-detail__header {
  padding-top: 50px;
  height: 600px;
  position: relative;
  z-index: 1;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  -webkit-box-shadow: 0px 1px 15px 0px rgba(0,0,0,0.5);
          box-shadow: 0px 1px 15px 0px rgba(0,0,0,0.5);
}

.weather-detail__header:before {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
  background-image: -webkit-gradient(linear,
    left top, left bottom,
    color-stop(-10%, #000),
    color-stop(80%, transparent));
  background-image: -webkit-linear-gradient(top,
    #000 -10%,
    transparent 80%);
  background-image: -o-linear-gradient(top,
    #000 -10%,
    transparent 80%);
  background-image: linear-gradient(to bottom,
    #000 -10%,
    transparent 80%);
}

.weather-detail__container {
  max-width: 1280px;
  margin: 0 auto;
  -outline: 2px dashed #fff;
}

.weather-detail__error-message {
  font-family: Roboto, sans-serif;
  position: relative;
  z-index: 5;
  font-size: 36px;
  font-weight: 400;
  text-align: center;
  color: #fff;
}


.weather-detail__main {
  -ms-flex-positive: 1;
      flex-grow: 1;
  background: #01A7A6;
}

.weather-detail__features {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.weather-detail__features-item {
  min-width: 200px;
  padding-top: 17px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  border-right: 4px solid rgba(1,129,128, 0.23);

  -outline: 1px solid red;
}

.weather-detail__features-item:last-child {
  border-right: none;
}

.weather-detail__feature-title {
  width: 152px;
  margin: 0 auto;
  margin-bottom: 30px;
  padding: 10px 0;
  text-align: center;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #fff;
  border-radius: 15px;
  background: #00807F;
}

.link-back {
  display: block;
  width: 30px;
  height: 30px;
  position: relative;
  top: 20px;
  left: 20px;
  z-index: 5;
  background: url(../img/icons/arrow.svg);
  background-repeat: no-repeat;
}


