.detail-temperature__container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;

  -outline: 1px dashed #fff;
}

.detail-temperature__number {
  font-family: Roboto, sans-serif;
  font-size: 160px;
  line-height: 125px;
  font-weight: 400;
  color: #fff;
}

.detail-temperature__extra {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;

  -outline: 1px dashed #fff;
}

.detail-temperature__icon {
  width: 142px;
  height: 100px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top;
}

.detail-temperature__description {
  font-family: Roboto, sans-serif;
  font-size: 20px;
  font-weight: 400;
  color: #fff;
}

.detail-temperature__description:first-letter {
  text-transform: capitalize;
}




