.cities__list-item {
  width: 470px;
  height: 60px;
  margin: 0 auto;
  margin-bottom: 30px;
  padding-left: 16px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background: #fff;
  border: none;
  border-radius: 20px;
  overflow: hidden;
  -webkit-box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.5);
          box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.5);
}

.cities__city-name {
  padding-left: 0;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 60px;
  color: #00807F;
}

.cities__remove-btn {
  padding: 10px 20px;
  display: block;
  font-family: Roboto, sans-serif;
  font-size: 20px;
  color: #fff;
  background: #FFB427;
  border: none;
}

@media screen and (max-width: 600px) {
  .cities__list-item {
    width: 90%;
  }
}


